body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
	background: white;
}

h1 {
  text-align: center;
}

nav > * {
  padding: 0 1rem;
}

li {
  list-style-type: none;
}

ul {
  list-style-type: none;
}

.notification {
  padding: 0 1rem;
  font-size: 2rem;
}

.filter-container {
  margin: 0 1rem;
}

.note-edit {
  color: black;
}
.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  -moz-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

.indent {
  display: block;
  list-style-type: disc;
  margin-top: 1em;
  margin: 0 0 1em;
  margin-left: 0;
  margin-right: 0;
  padding-left: 40px;
}

.notification {
  color: black;
  background: green;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin: 0 0 10px;
}

.error {
  color: black;
  background: red;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin: 0 0 10px;
}

.note-container {
  background-color: #eff2df;
  padding: 1rem;
}

.note-header {
  background-color: #e9f7ed;
  border: 1px solid transparent;
  border-radius: 4px;
  padding: 1rem;
}

.note-content {
  margin: 1rem 0;
  padding: 1rem;
}

.chip {
  margin: .5rem;
}

.login-separator {
  width: 100%;
  text-align: center;
  border-bottom: 1px solid #000;
  line-height: 0.1em;
  margin: 2rem 0;
}

.login-separator span {
  background:#fff;
  padding:0 10px;
}

.mystash-flex-item {
  display: flex;
  justify-content: center;
  margin: 1rem 0;
}
